@import '../../../assets/scss/variadmin.module.scss';

.topnav {
    padding: 15px 30px;
    padding-bottom: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: $topnav-height;
}

.topnav__search {
    position: relative;
    height: 50px;
    background-color: $main-bg;
    display: flex;
    align-items: center;
    box-shadow: $box-shadow;
    border-radius: $border-radius;
    overflow: hidden;
}

.topnav__search > input {
    height: 100%;
    width: 100%;
    padding: 10px 60px 10px 20px;
    font-size: 1rem;
    border-radius: $border-radius;
    color: $txt-color;
    background-color: $main-bg;
}

.topnav__search > i {
    font-size: 1.5rem;
    position: absolute;
    right: 20px;
}

.topnav__right {
    display: flex;
    align-items: center;
}

.topnav__right-item ~ .topnav__right-item {
    margin-left: 30px;
}

.notification-item {
    display: flex;
    align-items: center;
    padding: 20px;
}

.notification-item:hover {
    background-color: $second-bg;
}

.notification-item > i {
    margin-right: 20px;
    font-size: 1.5rem;
}

.topnav__right-user {
    display: flex;
    align-items: center;
}

.topnav__right-user__image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
}

.topnav__right-user__image > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.topnav__right-user__name {
    font-size: 1rem;
    font-weight: 600;
}
